  :root {
    --black: rgb(10, 10, 10);
    --white: rgb(50, 50, 50);
    --backround: var(--white);
    --foreground: var(--black);
    --transition: 0.5s ease;
  }

  
  .hidden {
    clip: rect(0 0 0 0);
    -webkit-clip-path: inset(50%);
            clip-path: inset(50%);
    height: 1px;
    overflow: hidden;
    position: absolute;
    white-space: nowrap;
    width: 1px;
  }
  
  .toggle-wrapper {
    width: 130px;
    display: block;
    margin: auto; /* Centering for demo */
  }
  
  .toggle {
    height: 65px;
    width: 130px;
    background: rgb(10, 10, 10);
    background: var(--foreground);
    border-radius: 40px;
    padding: 12px;
    position: relative;
    transition: background 0.5s ease;
    transition: background var(--transition);
    cursor: pointer;
  }
  
  .toggle::before {
    content: "";
    display: block;
    height: 41px;
    width: 41px;
    border-radius: 30px;
    background: var(--background);
    position: absolute;
    z-index: 2;
    -webkit-transform: translate(0);
            transform: translate(0);
    transition: background 0.5s ease, -webkit-transform 0.5s ease;
    transition: transform 0.5s ease, background 0.5s ease;
    transition: transform 0.5s ease, background 0.5s ease, -webkit-transform 0.5s ease;
    transition: background var(--transition), -webkit-transform var(--transition);
    transition: transform var(--transition), background var(--transition);
    transition: transform var(--transition), background var(--transition), -webkit-transform var(--transition);
  }
  
  .toggle.enabled::before {
    -webkit-transform: translateX(65px);
            transform: translateX(65px);
  }
  
  .toggle input {
    opacity: 0;
    position: absolute;
    top: 0;
  }
  
  .toggle .icons {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
    margin: 0 5px;
  }
  
  .toggle .icons svg {
    fill: var(--background);
    height: 30px;
    width: 30px;
    z-index: 0;
  }
  #themeChanger {
      -webkit-transform: scale(0.4);
              transform: scale(0.4);
  }
  

#load {
    position:absolute;
    width:600px;
    height:36px;
    left:50%;
    top:40%;
    margin-left:-300px;
    overflow:visible;
    -webkit-user-select:none;
    user-select:none;
    cursor:default;
  }
  
  #load div {
    position:absolute;
    width:20px;
    height:36px;
    opacity:0;
    font-family:Helvetica, Arial, sans-serif;
    animation:move 2s linear infinite;
    -o-animation:move 2s linear infinite;
    -moz-animation:move 2s linear infinite;
    -webkit-animation:move 2s linear infinite;
    transform:rotate(180deg);
    -o-transform:rotate(180deg);
    -moz-transform:rotate(180deg);
    -webkit-transform:rotate(180deg);
    color:#35C4F0;
  }
  
  #load div:nth-child(2) {
    animation-delay:0.2s;
    -o-animation-delay:0.2s;
    -moz-animation-delay:0.2s;
    -webkit-animation-delay:0.2s;
  }
  #load div:nth-child(3) {
    animation-delay:0.4s;
    -o-animation-delay:0.4s;
    -webkit-animation-delay:0.4s;
    -webkit-animation-delay:0.4s;
  }
  #load div:nth-child(4) {
    animation-delay:0.6s;
    -o-animation-delay:0.6s;
    -moz-animation-delay:0.6s;
    -webkit-animation-delay:0.6s;
  }
  #load div:nth-child(5) {
    animation-delay:0.8s;
    -o-animation-delay:0.8s;
    -moz-animation-delay:0.8s;
    -webkit-animation-delay:0.8s;
  }
  #load div:nth-child(6) {
    animation-delay:1s;
    -o-animation-delay:1s;
    -moz-animation-delay:1s;
    -webkit-animation-delay:1s;
  }
  #load div:nth-child(7) {
    animation-delay:1.2s;
    -o-animation-delay:1.2s;
    -moz-animation-delay:1.2s;
    -webkit-animation-delay:1.2s;
  }
  
  @keyframes move {
    0% {
      left:0;
      opacity:0;
    }
      35% {
          left: 41%;
          -webkit-transform:rotate(0deg);
          transform:rotate(0deg);
          opacity:1;
      }
      65% {
          left:59%; 
          -webkit-transform:rotate(0deg);
          transform:rotate(0deg); 
          opacity:1;
      }
      100% {
          left:100%; 
          -webkit-transform:rotate(-180deg); 
          transform:rotate(-180deg);
          opacity:0;
      }
  }
  
  @-webkit-keyframes move {
      0% {
          left:0; 
          opacity:0;
      }
      35% {
          left:41%; 
          -webkit-transform:rotate(0deg); 
          transform:rotate(0deg); 
          opacity:1;
      }
      65% {
          left:59%; 
          -webkit-transform:rotate(0deg); 
          transform:rotate(0deg); 
          opacity:1;
      }
      100% {
          left:100%;
          -webkit-transform:rotate(-180deg); 
          transform:rotate(-180deg); 
          opacity:0;
      }
  }
